





























































	export default {
		data() {
			return {
				year_title:'选择年份',
				month_title:'选择月份',
				day_title:'选择日期',
				yearsList: [],
				nomthList:[],
				dayList:[],
				animation:'',
				show_y:false,
				show_m:false,
				show_d:false
			}
		},
		onLoad() {
			this.getDateList();
			this.getYearsList(1992);
			this.getMonthList();
			this.DayList();
		},
		methods:{
			issue_rules(){
				uni.navigateTo({
					url:'../personal-center/issue_rules'
				})
			},
			getDateList(){
				// var token = (uni.getStorageSync("user_info")).token;
				var token=(uni.getStorageSync("user_info")).token;
				console.log(token);
				var d = new Date();
				var year = d.getFullYear();
				var month= d.getMonth()+1;
			    var day= d.getDate();
				uni.request({
					url:'http://bus.liebianzhe.com/api/user/month_bonus',
					data:{
						token:token,
						year:year,
						month:month,
						day:day
					},
					success: (res) => {
						console.log(res)
					}
				})
			},
			change_list(){
				var token=(uni.getStorageSync("user_info")).token;
				var year = this.year_title;
				var month = this.month_title;
				var day = this.day_title;
				if(year=='选择年份'){
					return
				}
				if(month=='选择月份'){
					return
				}
				if(day=='选择日期'){
					return
				}
				uni.request({
					url:'http://bus.liebianzhe.com/api/user/month_bonus',
					data:{
						token:token,
						year:year,
						month:month,
						day:day
					},
					success: (res) => {
						console.log(res)
					}
				})
			},
			getYearsList(yearNum){
				var d = new Date();
				var years = d.getFullYear();
				var yearTotal = years - yearNum;
				
				for(var i=0;i<=yearTotal;i++){
						var new_year = years--;
						this.yearsList.push(new_year)
				}
				// console.log(this.yearsList)
			},
			getMonthList(){
				// var d = new Date();
				var monthNum = 1;
				var num = 11;
				// var month = d.getMonth();
				for (var i=0;i<=num;i++){
					var new_month = monthNum++;
					this.nomthList.push(new_month)
				}
				// console.log(this.nomthList)
			},
			DayList(){
				var DayTotal=30;
	     	    var yearNum=1;
				for(var i=0;i<=DayTotal;i++){
            		var new_day = yearNum++;
					this.dayList.push(new_day)
				}
				// console.log(this.dayList)
			},
			show_year(){
				this.show_y = true;
				this.show_m = false;
				this.show_d = false;				
			},
			hid_year(value){
				this.show_y = false;
				this.year_title = value +'年';
			},
			show_month(){
				this.show_y = false;
				this.show_m = true;
				this.show_d = false
			},
			hid_month(value){
				this.show_m = false;
				this.month_title = value + '月';
			},
			show_day(){
				this.show_y = false;
				this.show_m = false;
				this.show_d = true;
			},
			hid_day(value){
				this.show_d = false;
				this.day_title = value + '日';
			}
		}
	}
